#sticky-2{
    border-left:4px solid $aqua_blue;
    border-right:4px solid $aqua_blue;
    @include flex(
            $flex-display:flex,
            $flex-direction:column,
            $flex-wrap:nowrap,
            $flex-align:center,
            $flex-justify:flex-start,
            );
    @include media(small){
        @include flex(
            $flex-display:flex,
            $flex-direction:row,
            $flex-wrap:nowrap,
            $flex-align:center,
            $flex-justify:flex-start,
            );
    }
    @include media (medium){
        @include flex(
            $flex-display:flex,
            $flex-direction:row,
            $flex-wrap:nowrap,
            $flex-align:center,
            $flex-justify:flex-start,
            );
    }
    @include media(large){
        border-left:none;
        border-right:none;
        border-top:4px solid $aqua_blue;
        width:100%;
        padding-top: 20px;
        padding-bottom: 20px;
        border-bottom:4px solid $aqua_blue;
        @include flex(
            $flex-display:flex,
            $flex-direction:column,
            $flex-wrap:nowrap,
            $flex-align:center,
            $flex-justify:space-around,
            );
    }
    @include media(x-large){
        border-left:none;
        border-right:none;
        border-top:4px solid $aqua_blue;
        width:100%;
        padding-top: 20px;
        padding-bottom: 20px;
        border-bottom:4px solid $aqua_blue;
        @include flex(
            $flex-display:flex,
            $flex-direction:column,
            $flex-wrap:nowrap,
            $flex-align:center,
            $flex-justify:space-around,
            );
    }
    @extend %center;
    //width:50%;
    
    height: 100%;
}

